$navbar-logo-font-size: 36px;
$navbar-logo-image-width: 45px;
$navbar-logo-image-height: 40px;
$navbar-font-size: $base-font-size * 1.18;
$navbar-margin: 0.5vw;
$navbar-horizontal-padding: 15px;

#navbar {
  position: fixed;
  top: 0;
  z-index: 101;

  background-color: $gold;
  height: 80px;
  width: 100vw;

  box-shadow: 0 5px 30px -5px rgba(0, 0, 0, 0.2);

  .container {
    @include flex(row nowrap, center, center);
    display: flex;
    height: 100%;
    width: 100%;
    padding: 0 $small-space;
    list-style-type: none;
  }
}

#navbar-logo {
  flex: 1;
  margin-top: -0.5vh;
  font-size: $navbar-logo-font-size;
  padding: 0 $navbar-horizontal-padding 0 $navbar-horizontal-padding;

  a {
    @include flex(row nowrap, flex-start, center);
  }

  img {
    fill: #fff;
    width: $navbar-logo-image-width;
    height: $navbar-logo-image-height;
  }
}

.menu {
  width: 100%;
  max-width: 1300px;
  display: flex;
  align-items: center;
  list-style-type: none;
  text-align: center;
  li {
    display: inline-block;
    vertical-align: middle;
  }
}

@media screen and (max-width: 430.68px) {
  .navbar-item {
    font-size: $navbar-font-size * 0.6 !important;
  }
}

.navbar-item {
  margin: 10px $navbar-margin;
  padding: 0 $navbar-horizontal-padding 0 $navbar-horizontal-padding;
  font-size: $navbar-font-size;

  a {
    color: #000;
  }

  &:hover a {
    color: $offwhite;
  }
}

.special-link a {
  color: $orange;
}

.apply-link {
  color: $white;
  background-color: $yellow;
  padding: 9px;
  font-weight: bold;
  border-radius: 0.25rem;
  transition: $transition-speed all;
  display: inline-block;

  &:hover,
  &:focus {
    transform: scale(1.05);
    // color: #fff;
  }
}

#navbar-apply {
  margin-left: $navbar-margin;
  padding: $navbar-horizontal-padding $navbar-margin * 1.5;
  font-size: $navbar-font-size;
  font-weight: $heavy-font-weight;
  transition: background-color $transition-speed;

  // &:hover {
  //   background-color: $main-green;
  // }
}

#title {
  color: #000;
  margin-left: 1vw;
}

@media screen and (max-width: 1220px) {
  .menu {
    display: block;
  }

  .navbar-item {
    margin: $navbar-margin;
  }

  #navbar-logo,
  #title {
    visibility: hidden;
    display: none;
  }

  .navbar-item.about-link {
    display: none;
  }

  .navbar-item a#home-tab {
    display: none;
  }

  .navbar-item a#sponsors-tab {
    display: none;
  }
}
